import './FabricantePromocionUpdate.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemField, Page, PhotoLoaderModalRegistro } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE } from '../../constants'
import { DetailViewProps, useToaster } from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'
import { ImagenProducto } from '../../services/api'
import { DATE_FORMAT } from '../../utils/date'

const QRCode = require('qrcode.react')
const moment = require('moment-timezone')
const numeral = require('numeral')

interface ImagenSeleccionada {
  name: string
  file: File | Blob
}

export const FabricantePromocionUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Producto, setProducto] = useState(null as any)
  const [Promocion, setPromocion] = useState({} as any)
  const [Errors, setErrors] = useState({} as any)
  const [Loading, setLoading] = useState(false)
  const addToast = useToaster()
  const [FotoPrincipalPreview, setFotoPrincipalPreview] = useState(null as any)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (match.params) {
      if (match.params.id) {
        obtenerPromocion()
      }
      obtenerProducto()
    }
  }, [])

  const obtenerPromocion = () => {
    setLoading(true)
    const params = match.params as any
    rootStore.environment.api.simple_get(`/promocion/${params.id}/`).then((result) => {
      if (result.kind === 'ok') {
        const data = { ...result.data }
        // Configurar fecha/hora
        const fecha_desde = moment(data.fecha_desde)
        const fecha_hasta = moment(data.fecha_hasta)
        data.hora_desde = fecha_desde.format(DATE_FORMAT.HOURMINUTE)
        data.hora_hasta = fecha_hasta.format(DATE_FORMAT.HOURMINUTE)
        data.fecha_desde = fecha_desde.format(DATE_FORMAT.DATE)
        data.fecha_hasta = fecha_hasta.format(DATE_FORMAT.DATE)
        setPromocion(data)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  const obtenerProducto = () => {
    rootStore.environment.api.getProductoAdmin(match.params.producto).then((result) => {
      if (result.kind === 'ok') {
        setProducto(result.data)
        //
        if (result.data.imagenes.length) {
          const portada = result.data.imagenes.find((img: ImagenProducto) => img.portada)
          if (portada) {
            setFotoPrincipalPreview(portada.imagen)
          }
        }
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  const setDato = (campo: string, valor: number | string) => {
    let datos = { ...Promocion }
    datos[campo] = valor
    setPromocion(datos)
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const setCodigoDescuento = () => {
    if (Promocion.codigo_descuento) {
      setDato('codigo_descuento', '')
      return
    }
    setDato('codigo_descuento', Math.floor(Math.random() * 10000000))
  }

  const guardar = async () => {
    const datos = { ...Promocion }
    const errors = {} as any
    // Validaciones
    const horaRegex = /^([0-9]{2}:[0-9]{2})$/
    const fechaRegex = /^([0-9]{2}\/[0-9]{2}\/[0-9]{4})$/
    if (!fechaRegex.test(datos.fecha_desde)) {
      errors.fecha_desde = 'Ingrese una fecha válida'
    }
    if (!horaRegex.test(datos.hora_desde)) {
      errors.hora_desde = 'Ingrese una hora válida'
    }
    if (!fechaRegex.test(datos.fecha_hasta)) {
      errors.fecha_hasta = 'Ingrese una fecha válida'
    }
    if (!horaRegex.test(datos.hora_hasta)) {
      errors.hora_hasta = 'Ingrese una hora válida'
    }
    if (Object.keys(errors).length) {
      setErrors(errors)
      addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
      return
    }
    // Combinar fecha-hora
    datos.fecha_desde = moment(`${datos.fecha_desde} ${datos.hora_desde}:00`, DATE_FORMAT.DATETIME).format(
      DATE_FORMAT.ISO,
    )
    datos.fecha_hasta = moment(`${datos.fecha_hasta} ${datos.hora_hasta}:00`, DATE_FORMAT.DATETIME).format(
      DATE_FORMAT.ISO,
    )
    datos.producto = Producto.id
    if (datos.tipo === 10) {
      datos.precio = null
    }
    //
    setErrors({} as any)
    setLoading(true)
    const url = `/promocion${Promocion.id ? `/${Promocion.id}` : ''}/`
    let result
    if (Promocion.id) {
      result = (await rootStore.environment.api.simple_patch(url, datos)) as any
    } else {
      result = (await rootStore.environment.api.simple_post(url, datos)) as any
    }
    // const apiCall = Promocion.id ? rootStore.environment.api.simple_patch : rootStore.environment.api.simple_post
    // apiCall(url, datos).then((result: any) => {
    setLoading(false)
    if (result.kind === 'ok') {
      addToast('Promoción guardada exitosamente', 'success')
      history.replace(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(Producto.id))
    } else if (result.kind === 'bad-data') {
      addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
      setErrors(result.errors)
      if (result.errors.non_field_errors) {
        alert(result.errors.non_field_errors[0])
      }
    } else {
      addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
    }
    // })
  }

  const eliminar = () => {
    if (!window.confirm('Confirma que desea eliminar la promoción?')) {
      return
    }
    setLoading(true)
    rootStore.environment.api.simple_delete(`/promocion/${Promocion.id}/`).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Promoción eliminada exitosamente', 'success')
        history.replace(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(Producto.id))
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const inputHoraDesde = (
    <AdminItemField
      titulo={''}
      tipo={'hora'}
      campo={'hora_desde'}
      valor={Promocion.hora_desde}
      error={Errors && Errors.hora_desde}
      set_dato={setDatoInput}
      columnas={4}
    />
  )

  const inputHoraHasta = (
    <AdminItemField
      titulo={''}
      tipo={'hora'}
      campo={'hora_hasta'}
      valor={Promocion.hora_hasta}
      error={Errors && Errors.hora_hasta}
      set_dato={setDatoInput}
      columnas={4}
    />
  )

  const renderContent = () => (
    <>
      <SubHeader
        links={[
          { titulo: 'Productos', ruta: RUTAS_APP_FABRICANTE.CATALOGO },
          { titulo: Producto.nombre, ruta: RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(Producto.id) },
          { titulo: Promocion.id ? 'Editar promoción' : 'Crear promoción' },
        ]}
      />
      <div className={Movil ? 'container-lg' : ''}>
        <div className={Movil ? 'row' : 'd-flex flex-row pl-3 flex-grow-1'}>
          <div className={Movil ? 'col-12' : ''}>
            <PhotoLoaderModalRegistro
              titulo_modal={''}
              titulo_label={''}
              helptext={''}
              img_path={''}
              on_select={() => {}}
              userdata={''}
              preview={FotoPrincipalPreview}
              source={0}
              editor={false}
              perfil={true}
              open_modal={false}
              disable={true}
            />
          </div>

          <div className={Movil ? 'col-12' : 'd-flex flex-column pl-4 flex-grow-1 justify-content-center'}>
            <div className="d-flex flex-row align-items-center">
              <div className="input-nombre">{Producto.nombre}</div>
            </div>
            <div>{`Id#${Producto.id}`}</div>
            {loginStore.fabricante && (
              <div>
                {loginStore.fabricante.rating}
                <FIcon.Star size={'1rem'} color="var(--oro)" className="ml-1" />
              </div>
            )}
          </div>
        </div>
        <div className={'row mt-4 mt-lg-0'}>
          <AdminItemField
            titulo={'Nombre'}
            placeholder="Nombre de la promoción (Ej.: Liquidación)"
            tipo={'texto'}
            campo={'nombre'}
            valor={Promocion.nombre}
            error={Errors && Errors.nombre}
            set_dato={setDatoInput}
            columnas={12}
          />
          <div className="col-12 col-lg-4 mb-3">
            <label className={'font-weight-bold'}>Tipo de promoción</label>
            <div className="d-flex flex-row">
              <div className="chk-promocion">
                <div className="input" onClick={() => setDato('tipo', 10)}>
                  {Promocion.tipo === 10 && <FIcon.Check size={24} color={'var(--verde-claro)'} />}
                </div>
                2x1
              </div>
              <div className="chk-promocion">
                <div className="input" onClick={() => setDato('tipo', 20)}>
                  {Promocion.tipo === 20 && <FIcon.Check size={24} color={'var(--verde-claro)'} />}
                </div>
                Descuento
              </div>
            </div>
            {Errors.tipo && <p className="text-danger mt-1">{Errors.tipo}</p>}
          </div>
          <AdminItemField
            titulo={'Precio promocional (Gs.)'}
            tipo={'numero'}
            campo={'precio'}
            valor={Promocion.precio}
            error={Errors && Errors.precio}
            set_dato={setDatoInput}
            columnas={4}
            deshabilitado={Promocion.tipo === 20}
          />
          <div className="col-12 col-lg-4 mb-3">
            <div className="chk-promocion" style={{ marginTop: Movil ? 0 : '1.4rem' }}>
              <div className="input" onClick={setCodigoDescuento}>
                {Promocion.codigo_descuento && <FIcon.Check size={24} color={'var(--verde-claro)'} />}
              </div>
              Usar código ABi de promoción
            </div>
          </div>
          <AdminItemField
            titulo={'Desde'}
            tipo={'fecha'}
            campo={'fecha_desde'}
            valor={Promocion.fecha_desde}
            error={Errors && Errors.fecha_desde}
            set_dato={setDatoInput}
            columnas={4}
            extra_css="input-fecha"
          />
          {Movil && inputHoraDesde}
          <AdminItemField
            titulo={'Hasta'}
            tipo={'fecha'}
            campo={'fecha_hasta'}
            valor={Promocion.fecha_hasta}
            error={Errors && Errors.fecha_hasta}
            set_dato={setDatoInput}
            columnas={4}
            extra_css="input-fecha"
          />
          {Movil && inputHoraHasta}
          <div className="col-12 col-lg-4" />
          {!Movil && inputHoraDesde}
          {!Movil && inputHoraHasta}
          {Promocion.codigo_descuento && (
            <>
              <div className="col-12">
                <p>
                  Los compradores podrán escanear el código QR para acceder a la promoción. ¡Descarga el código para
                  incluirlo en tus redes sociales! O bien podes utilizar el código númerico.
                </p>
              </div>
              <div className="col-12 col-lg-4 mb-4">
                <div style={{ position: 'relative', width: '196px', height: '196px', margin: 'auto' }}>
                  <QRCode
                    value={`https://abi.com.py/?codigo=${Promocion.codigo_descuento}`}
                    size={196}
                    fgColor={'#303ab2'}
                  />
                  <span className="icono-abi-qr">{ICONOS.abi}</span>
                </div>
              </div>
              <div className="col-12 col-lg-4 d-flex flex-column justify-content-center mb-4 text-center text-lg-left">
                <label className={'font-weight-bold'}>Código de descuento</label>
                <div className="codigo-descuento">{Promocion.codigo_descuento}</div>
              </div>
            </>
          )}
          <div className="col-12 mb-4">
            <button className="btn btn-danger px-4" onClick={() => history.goBack()}>
              Cancelar
            </button>
            {Promocion.id && (
              <button className="btn btn-danger px-4 ml-3" onClick={eliminar}>
                Eliminar
              </button>
            )}
            <button className="btn btn-primary px-4 ml-3" onClick={guardar}>
              {Promocion.id ? 'Guardar' : 'Lanzar promoción'}
            </button>
          </div>
        </div>
      </div>
    </>
  )

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'fabricante-promocion-update'}>
      {(!Producto || Loading) && <IonSpinner name="crescent" className={'loadingSpinner'} />}
      {Producto && !Loading && renderContent()}
    </Page>
  )
})

export default FabricantePromocionUpdate
